import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { FeedbackService } from 'src/app/service/feedback.service';
declare var swal: any;


@Component({
  selector: 'app-feedback-add',
  templateUrl: './feedback-add.component.html',
})
export class FeedbackAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  paymentTypeList: any = [];
  showLoader = false;
  formErrors = {
    name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private feedbackservice: FeedbackService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      name: [null, Validators.compose([Validators.required])],
      email: [null, Validators.compose([Validators.required,Validators.email])],
      feedback_subject: [null, Validators.compose([Validators.required])],
      feedback_massage: [null],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.feedbackservice.getFeedbackById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            name: response.data.name,
            email: response.data.email,
            feedback_massage: response.data.feedback_massage,
            feedback_subject: response.data.feedback_subject
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/feedback');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.feedbackservice.editFeedback(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/feedback');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }

            });
          }
        });
      } else {
        this.feedbackservice.addFeedback(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/feedback');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }
            });
          }
        });
      }
    }
  }
}
